@font-face {
  font-family: 'Muli-Bold';
  src: url("../fonts/Muli-Bold.ttf");
  font-display: swap;
  font-weight: 700; }

@font-face {
  font-family: 'Muli-BoldItalic';
  src: url("../fonts/Muli-BoldItalic.ttf");
  font-display: swap; }

@font-face {
  font-family: 'Muli-ExtraBold';
  src: url("../fonts/Muli-ExtraBold.woff");
  font-display: swap; }

@font-face {
  font-family: 'Muli-Italic';
  src: url("../fonts/Muli-Italic.ttf");
  font-display: swap;
  font-style: italic; }

@font-face {
  font-family: 'Muli-Regular';
  src: url("../fonts/Muli-Regular.woff");
  font-display: swap; }

@font-face {
  font-family: 'Muli-Semi_BoldItalic';
  src: url("../fonts/Muli-Semi-BoldItalic.ttf");
  font-display: swap;
  font-style: italic; }

* {
  border: 1px solid red;
  box-sizing: border-box; }

body {
  font-family: Muli-Regular, sans-serif;
  color: #fff; }

.header {
  background-image: url(../images/header-main-img.png);
  height: 100vh;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: #fff; }

.container {
  max-width: 1720px;
  margin: 0 auto;
  padding: 10px; }

.header__top {
  padding: 90px 0 105px;
  /*width: 100%;*/
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.menu__list {
  display: flex;
  align-items: center; }

.menu__item {
  margin-right: 35px; }

.menu__item:last-child {
  margin-right: 0px; }

.menu__link {
  font-family: 'Muli-Bold', sans-serif;
  font-size: 16px;
  text-transform: uppercase;
  color: #fff; }

.header__logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%); }

.header__personal {
  width: 420px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.personal {
  margin-right: 45px; }

.personal__list {
  display: flex;
  align-items: center;
  position: relative; }

.personal__list::after {
  content: "";
  height: 2px;
  width: 48px;
  background: #fff;
  opacity: 0.3;
  display: block;
  position: absolute;
  right: -96px;
  top: 12px; }

.personal__list-item {
  margin-right: 45px; }

.personal__list-item:last-child {
  margin-right: 0; }

.personal__link-cart {
  position: relative; }

.personal__cart-count {
  font-family: Muli-Regular;
  font-size: 10px;
  display: inline-block;
  height: 18px;
  width: 18px;
  background-color: #fff;
  text-align: center;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  position: absolute;
  top: -13px;
  right: -15px; }

.social__list {
  display: flex;
  align-items: center; }

.social__list-item {
  margin-right: 10px; }

.social__link {
  height: 35px;
  width: 35px;
  background-color: #fff;
  text-align: center;
  line-height: 2.3;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%; }

.header__content {
  display: flex;
  justify-content: center; }

.btn {
  font-family: 'Muli-ExtraBold', sans-serif;
  font-size: 21px;
  width: 240px;
  height: 60px;
  padding: 0 38px;
  border-radius: 35px;
  -webkit-border-radius: 35px;
  -moz-border-radius: 35px;
  -ms-border-radius: 35px;
  -o-border-radius: 35px;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header__btn {
  font-size: 24px;
  line-height: 40px;
  width: 280px;
  height: 70px;
  /*display: flex;
		justify-content: space-between;*/ }

.btn--green {
  background-color: #0aa566; }

/*header end*/
.smoothies {
  font-family: Muli-Regular;
  color: #000; }

.smoothies__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center; }

.title {
  font-family: Muli-ExtraBold;
  font-style: normal;
  font-size: 50px;
  line-height: 40px; }

.smoothies__title {
  /*max-width: 637px;*/
  max-height: 38px;
  color: #f7ae3b;
  margin: 100px 0 55px 0; }

.smoothies__text {
  font-size: 18px;
  line-height: 35px;
  max-width: 915px;
  text-align: center;
  margin-bottom: 90px; }

.smoothies__table {
  margin-bottom: 165px;
  display: flex;
  flex-wrap: wrap; }

.smoothies__table-item {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: column;
  width: 25%;
  height: 180px;
  border-right: 1px solid #f6edde;
  border-top: 1px solid #f6edde;
  /*padding: 0 0 27px 0;*/
  /*flex-direction: column;
		justify-content: center;
		align-items: center;*/ }

.smoothie__icon {
  width: 70px;
  height: 70px; }

.smoothies__subtitle {
  font-family: Muli-ExtraBold;
  font-style: normal;
  font-size: 25px;
  color: #f2612d;
  padding: 25px 0 35px 0; }

/*smoothies end*/
.about {
  background-color: #198073; }

.about__wrapper {
  display: flex;
  position: relative; }

.about__image {
  max-width: 691px;
  max-height: 818px;
  margin-right: 142px;
